import { Editor } from 'react-draft-wysiwyg'
import { EditorState, ContentState } from 'draft-js'
import { useState } from "react"

const ProductDescription = (props) => {
    const { product } = props
    const [desc, setDesc] = useState(product?.product_description ? EditorState.createWithContent(ContentState.createFromText(product?.product_description)) : EditorState.createEmpty())

    function saveEditorState(data) {
        setDesc(data)
        product.product_description = data.getCurrentContent().getPlainText()
    }
    return (
        <div style={{marginBottom: '25px'}}>
            <h2 className={'product-detail--title'}>
                Mô tả sản phẩm
            </h2>
            <Editor
                editorClassName='rounded-0'
                toolbarClassName='rounded-0 editor'
                editorState={desc}
                onEditorStateChange={data => saveEditorState(data)}
            />
        </div>
    )
}
export default ProductDescription
