// ** React Imports
import React, {useState} from 'react'
import '@styles/react/pages/product-detail.scss'
import {Card, CardBody, DropdownItem, DropdownMenu, DropdownToggle, Table, UncontrolledDropdown} from "reactstrap"
import currencyFormat from "../../../../utility/CurrencyFormat"
import {FileText, MoreVertical, Trash2} from "react-feather"
//img
const ProductVariant = props => {
    // ** Props
    const { product, variant_groups, setVariantFromTable} = props

    function handleViewVariantDetail(e, variant) {
        setVariantFromTable(variant)
    }
    const [indexActive, setIndexActive] = useState()

    return (
        <Card className={'mb-0'}>
            <CardBody className={'p-0'}>
                <div className={'table-contain'}>
                    <Table className={'table-product'} responsive>
                        <thead>
                        <tr>
                            <th scope='col' className='text-nowrap text-center'>
                                Ảnh
                            </th>
                            <th scope='col' className='text-nowrap'>
                                Tên phiên bản sản phẩm
                            </th>
                            <th scope='col' className='text-nowrap text-center'>
                                Giá
                            </th>
                            <th scope='col' className='text-nowrap text-center'>
                                Kích thước
                            </th>
                            <th scope='col' className='text-nowrap text-center'>
                                Màu
                            </th>
                            <th scope='col' className='text-nowrap text-center'>
                                Số lượng
                            </th>
                            <th scope='col' className='text-nowrap text-center'>
                                Tồn kho
                            </th>
                            <th scope='col' className='text-nowrap text-center' />
                        </tr>
                        </thead>
                        <tbody>
                        {
                            variant_groups?.map((variant_group, index) => (
                                <tr className={indexActive === index ? 'bg-grey' : ''} key={index} onClick={ e => {
                                    setIndexActive(index)
                                    handleViewVariantDetail(e, variant_group)
                                }}>
                                    <td className='text-nowrap'><div className={'table-img'}><img src={variant_group?.default?.thumb_url} alt=""/></div></td>
                                    <td className='text-nowrap'>{product?.name}</td>
                                    <td className='text-nowrap text-center'>{currencyFormat(variant_group?.default?.variant_retail_price)}đ</td>
                                    <td className='text-center'>{variant_group?.sizes.join(' - ')}</td>
                                    <td className='text-nowrap text-center'>{variant_group?.default?.color}</td>
                                    <td className='text-nowrap text-center'>{variant_group?.default?.color}</td>
                                    <td className='text-nowrap text-center'>{variant_group?.default?.color}</td>
                                    <td className='text-nowrap text-center'>
                                        <UncontrolledDropdown>
                                            <DropdownToggle tag='div' className='btn btn-sm'>
                                                <MoreVertical className='cursor-pointer' />
                                            </DropdownToggle>
                                            <DropdownMenu right>
                                                <DropdownItem
                                                    to={`/product/detail/${variant_group.id}`}
                                                    className='w-100'
                                                    onClick={() => {

                                                    }}
                                                >
                                                    <FileText size={14} className='mr-50' />
                                                    <span className='align-middle'>Chỉnh sửa</span>
                                                </DropdownItem>
                                                <DropdownItem className='w-100' onClick={() => {

                                                }}>
                                                    <Trash2 size={14} className='mr-50' />
                                                    <span className='align-middle'>Xóa</span>
                                                </DropdownItem>
                                            </DropdownMenu>
                                        </UncontrolledDropdown>
                                    </td>
                                </tr>
                            ))
                        }

                        </tbody>
                    </Table>
                </div>
            </CardBody>
        </Card>
    )
}

export default ProductVariant
