import React, {useEffect, useLayoutEffect, useState} from 'react'
import CreatableSelect from "react-select/creatable"
import instances from "../../../../@core/plugin/axios"
import {  Label } from 'reactstrap'

const ProductDefaultSpecification = (props) => {
    const { product, default_type_values} = props
    const [currentTypes, setCurrentType] = useState([])

    async function handleChange(newValue, currentType, index) {
        if (newValue?.value !== undefined &&
            newValue?.value !== null &&
            newValue?.value !== '') {
            const found = currentType?.product_types.filter((val) => val?.id === newValue?.value)
            if (found && found?.length === 0) {
                const request = {type_value_id: currentType.id, name: newValue.value}
                const response = await instances.post('/admin/product/type/add', request)
                if (response) {
                    const product_type = response?.data?.data
                    currentType?.product_types.push(product_type)
                    currentType?.product_type_converts.push({
                        current_type: currentType, value: product_type.id, label: product_type.name
                    })
                    found.push(product_type)
                }
            }
            const type = currentTypes[index]
            if (type) {
                type.current_type = currentType
                type.product_type =  found
                type.value = found[0]?.id
                type.label = newValue?.label
                setCurrentType([...currentTypes])
                product.product_types.push(type)
            }
        } else {
            const type = currentTypes[index]
            if (type) {
                type.current_type = currentType
                type.product_type =  null
                type.value = ''
                type.label = ''
                setCurrentType([...currentTypes])
                product.product_types.splice(product.product_types.indexOf(type), 1)
            }
        }
    }

    function checkValueInProduct(item, index) {
        let value = currentTypes[index]
        if (value === undefined) {
            value = {current_type: item, value: '', label: ''}
            if (product?.product_types !== undefined &&
                product?.product_types?.length > 0) {
                for (const product_has_type of product?.product_types) {
                    if (item.id === product_has_type.type_value_id) {
                        for (const product_type of item?.product_types) {
                            if (product_type.id === product_has_type?.product_has_type?.product_type_id) {
                                value = {
                                    current_type: item,
                                    product_type: product_type,
                                    value: product_type?.id,
                                    label: product_type?.name
                                }
                            }
                        }
                    }
                }
            }
            currentTypes[index] = value
            return currentTypes[index]
        } else {
            return currentTypes[index]
        }
    }

    return (
        <>
            <div>
            {
                default_type_values?.map((item, index) => (
                    <div key={index} className={'product-detail--item'}>
                        <Label className={'name'}>{item?.name}</Label>
                        <CreatableSelect
                            isClearable
                            value={checkValueInProduct(item, index)}
                            onChange={newValue => {
                                handleChange(newValue, item, index).then()
                            }
                            }
                            options={item?.product_type_converts}
                        />
                    </div>
                ))
            }
            </div>
        </>
    )
}
export default ProductDefaultSpecification
