// ** React Imports
import React, {useEffect, useState} from 'react'
import '@styles/react/pages/product-detail.scss'
// ** Third Party Components
import {Card, CardBody} from 'reactstrap'
//img
//icon
import {DragDrop} from "@uppy/react"
import Uppy from "@uppy/core"
import thumbnailGenerator from "@uppy/thumbnail-generator"
import {getVariantMedia, updateVariantProduct, uploadImages, uploadVideo} from "../../store/action"
import icDelete from "../../../../assets/images/icons/icons/ic_delete.svg"
import imgEmpty from "../../../../assets/images/icons/icons/ic_upload_img.svg"
import videoEmpty from "../../../../assets/images/icons/icons/ic_video_empty.svg"

const ProductVariantMedia = props => {
    // ** Props
    const { product, variantGroup } = props
    const [variant_id, setVariantId] = useState(null)
    const [variantImages, setVariantImages] = useState([])
    const [variantVideo, setVariantVideo] = useState({})
    const [isEdit, setEdit] = useState(false)
    const [isLoading, setLoading] = useState(false)

    useEffect(async () => {
        if (variantGroup && variantGroup?.default && (variant_id === null || variant_id !== variantGroup?.default?.id)) {
            setVariantId(variantGroup?.default?.id)
            const response = await getVariantMedia(product?.id, variantGroup?.default?.id)
            if (response?.data?.data) {
                const variant_media = response?.data?.data
                const variantImages = []
                let variantVideo = {}
                if (variant_media && variant_media.length > 0) {
                    for (const variantMedia of variant_media) {
                        if (variantMedia.type === 'image') {
                            variantImages.push(variantMedia)
                        } else if (variantMedia.type === 'video') {
                            variantVideo = variantMedia
                        }
                    }
                    setVariantImages(variantImages)
                    setVariantVideo(variantVideo)
                } else {
                    setVariantImages([])
                    setVariantVideo(null)
                }
            }
        }
    })

    const videoUppy = new Uppy({
        meta: { type: 'video' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['video/*'] }
    })

    videoUppy.on('file-added', async (file) => {
        const variantVideo = {}
        variantVideo.url = URL.createObjectURL(file.data)
        variantVideo.video_file = file.data
        variantVideo.media_type = 'video'
        setVariantVideo(variantVideo)
        setEdit(true)
    })

    const videoUppy2 = new Uppy({
        meta: { type: 'video' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['video/*'] }
    })

    videoUppy2.on('file-added', async (file) => {
        const variantVideo = {}
        variantVideo.url = URL.createObjectURL(file.data)
        variantVideo.video_file = file.data
        variantVideo.media_type = 'video'
        setVariantVideo(variantVideo)
        setEdit(true)
    })

    function deleteVideo(event) {
        setVariantVideo({})
        setEdit(true)
    }

    const renderVideoPreview = () => {
        if (variantVideo && variantVideo?.url) {
            return (
                <div className={'rounded'}>
                    <div className={'video-contain position-relative'}>
                        <video autoPlay playsInline loop className={'video'}>
                            <source src={variantVideo?.url}/>
                        </video>
                        {
                            <div className={'btn-delete'}>
                                <img src={icDelete} alt="delete" className={'icon-delete'} onClick={e => {
                                    deleteVideo(e)
                                }}/>
                            </div>
                        }
                    </div>
                </div>
            )
        } else {
            return (
                <div className={'empty'}>
                    <div className={'empty-image'}>
                        <img className={'img-contain'} src={videoEmpty} alt="empty"/>
                    </div>
                    <DragDrop uppy={videoUppy2} />
                </div>
            )
        }
    }

    function deleteImage(event, variantImage) {
        variantImages.splice(variantImages.indexOf(variantImage), 1)
        setVariantImages([...variantImages])
        setEdit(true)
    }

    const imageUppy = new Uppy({
        meta: { type: 'avatar' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['image/*'] }
    })

    imageUppy.use(thumbnailGenerator)

    imageUppy.on('thumbnail:generated', (file, preview) => {
        const arr = { file: file, preview: preview}
        variantImages.push(arr)
        setVariantImages([...variantImages])
        setEdit(true)
    })

    const imageUppy2 = new Uppy({
        meta: { type: 'avatar' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['image/*'] }
    })

    imageUppy2.use(thumbnailGenerator)

    imageUppy2.on('thumbnail:generated', (file, preview) => {
        const arr = { file: file, preview: preview}
        variantImages.push(arr)
        setVariantImages([...variantImages])
        setEdit(true)
    })

    const renderPreview = () => {
        if (variantImages.length) {
            return (
                <div className={'product-detail--image-product'}>
                    {
                        variantImages.map((value, index) => (
                            <div key={index} className={'rounded'}>
                                <div className={'image'}>
                                    <img src={value?.thumb_url ? value?.thumb_url : value?.preview} alt="imgProduct" className={'img'}/>
                                    {
                                        <div className={'btn-delete'}>
                                            <img src={icDelete} alt="delete" onClick={e => {
                                                deleteImage(e, value)
                                            }}/>
                                        </div>
                                    }
                                </div>
                            </div>
                        ))
                    }
                </div>
            )
        } else {
            return (
                <div className={'empty'}>
                    <div className={'empty-image'}>
                        <img className={'img-contain'} src={imgEmpty} alt="empty"/>
                    </div>
                    <DragDrop uppy={imageUppy2} />
                </div>
            )
        }
    }

    async function handleSubmitButton() {
        if (variantImages.length > 0 || variantVideo) {
            setEdit(false)
            setLoading(true)
            const formData = new FormData()
            let i = 0
            let isUpload = false
            const variantImageInserts = []
            variantImages.forEach(variantImage => {
                if (typeof variantImage?.id !== 'number' && variantImage?.url === undefined) {
                    isUpload = true
                    formData.append(`images`, variantImage.file.data)
                    i++
                } else {
                    variantImageInserts.push(variantImage)
                }
            })
            if (isUpload) {
                const files = await uploadImages(formData)
                variantImages.forEach(variantImage => {
                    files?.data?.data?.forEach(file => {
                        if (variantImage?.file?.data?.name === file.originalname) {
                            variantImage.file_path = file.originalname
                            variantImage.file_name = file.filename
                            variantImage.url = file.url
                            variantImage.thumb_url = file.thumb_url
                            variantImage.mimetype = file.mimetype
                            const image_detail = {}
                            image_detail.file_path = file.originalname
                            image_detail.file_name = file.filename
                            image_detail.url = file.url
                            image_detail.thumb_url = file.thumb_url
                            image_detail.mimetype = file.mimetype
                            variantImageInserts.push(image_detail)
                        }
                    })
                })
            }

            const video_detail = {}
            if (variantVideo?.video_file) {
                const formData = new FormData()
                formData.append(`video`, variantVideo.video_file)
                const response = await uploadVideo(formData)
                if (response?.data?.data) {
                    const video = response.data.data
                    video_detail.file_path = video?.originalname
                    video_detail.file_name = video?.filename
                    video_detail.url = video?.url
                    video_detail.media_type = 'video'
                }
            }
            const productVariantMedia = []
            for (const variant of variantGroup.variants) {
                for (const image of variantImageInserts) {
                    const item = {}
                    item.type = 'image'
                    item.option = variant.color && variant.color !== '' ? variant.color : 'default'
                    item.url = image.url
                    item.thumb_url = image.thumb_url
                    item.variant_id = variant.id
                    if (productVariantMedia.indexOf(item) === -1) {
                        productVariantMedia.push(item)
                    }
                }
                if (video_detail && video_detail?.url) {
                    const item1 = {}
                    item1.type = 'video'
                    item1.option = variant.color && variant.color !== '' ? variant.color : 'default'
                    item1.url = video_detail.url
                    item1.variant_id = variant.id
                    productVariantMedia.push(item1)
                }
            }
            const productMedia = []
            let position = 0
            const images = {}
            for (const image of variantImageInserts) {
                position++
                if (position === 2) {
                    position = 3
                }
                const item = {}
                if (position === 1) {
                    images.image_url = image.url
                    images.thumb_url = image.thumb_url
                }
                item.media_type = 'image'
                item.option = variantGroup?.default?.color && variantGroup?.default?.color !== '' ? variantGroup?.default?.color : 'default'
                item.url = image.url
                item.thumb_url = image.thumb_url
                item.product_id = product?.id
                item.position = position
                productMedia.push(item)
            }
            if (video_detail?.url) {
                const item = {}
                item.media_type = 'video'
                item.option = variantGroup?.default?.color && variantGroup?.default?.color !== '' ? variantGroup?.default?.color : 'default'
                item.url = video_detail.url
                item.product_id = product?.id
                item.position = 2
                productMedia.push(item)
            }
            await updateVariantProduct(productMedia, productVariantMedia, images, product?.id,
                variantGroup?.default?.color && variantGroup?.default?.color !== '' ? variantGroup?.default?.color : 'default')
            setLoading(false)
        }
    }

    return variantGroup !== null ? (
        <Card className={'product-variant-item__card border-dark'}>
            <CardBody className={'py-2'}>
                <div className={'product-detail--contain'}>
                    {
                        isLoading &&
                        <div className='loading'>
                            <div className='effect-1 effects'/>
                            <div className='effect-2 effects'/>
                            <div className='effect-3 effects'/>
                        </div>
                    }
                    <div className={'product-detail--contain'}>
                        <div className={'d-flex justify-content-end'}>
                            <button className={'btn btn-primary mr-0 w-25'} disabled={!isEdit} onClick={handleSubmitButton}>Lưu</button>
                        </div>
                        <h2 className={'product-detail--title'}>
                            Tên phiên bản sản phẩm
                        </h2>
                        <input className={'product-detail--name'} type="text" value={ variantGroup?.default?.name } onChange={ event => { console.log(event) }}/>
                    </div>
                    <div className={'d-flex justify-content-between align-items-center'} style={{marginBottom: '10px'}}>
                        <h3 className={'product-detail--title product-detail-title__detail mb-0'}>
                            Hình ảnh phiên bản
                        </h3>
                        <div className={'product-detail--add-image'}>
                            <DragDrop uppy={imageUppy} accept="image/png, image/jpeg"
                                      locale={{
                                          strings: {dropHereOr: "Thêm hình ảnh"}
                                      }}
                            />
                        </div>
                    </div>
                    <div className={'product-detail--contain-image'}>
                        {renderPreview()}
                    </div>
                </div>
                <div className={'product-detail--contain'}>
                    <div className={'d-flex justify-content-between align-items-center'} style={{marginBottom: '10px'}}>
                        <h3 className={'product-detail--title product-detail-title__detail mb-0'}>
                            Video phiên bản
                        </h3>
                        <div className={'product-detail--add-image'}>
                            <DragDrop uppy={videoUppy} accept="video/mp4,video/x-m4v,video/*"
                                      locale={{
                                          strings: {dropHereOr: "Thêm video"}
                                      }}
                            />
                        </div>
                    </div>
                    <div className={'product-detail--contain-image'}>
                        <div className={'d-flex justify-content-center align-items-center'}>
                            {renderVideoPreview()}
                        </div>
                    </div>
                </div>

            </CardBody>
        </Card>
        ) : (<div></div>)
}

export default ProductVariantMedia
