// ** React Imports
import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import '@styles/react/pages/product-detail.scss'
// ** Third Party Components
import {Card, CardBody, Label} from 'reactstrap'
import CreatableSelect from "react-select/creatable/dist/react-select.esm"
//img
//icon
const ProductInformation = props => {
    // ** Props
    const { product } = props

    const [colors, setColors] = useState([])

    const [sizes, setSizes] = useState([])

    // ** Condition btn tag
    useEffect(() => {
        if (product) {
            const options = product?.product_options
            options?.forEach(option => {
                if (option?.name.toLowerCase() === 'kích thước') {
                    setSizes(option.values)
                }
                if (option?.name.toLowerCase() === 'màu sắc') {
                    setColors(option.values)
                }
            })
        }
    })

    return (
        <div>
            <Card className={'border-dark'}>
                <CardBody>
                    <div className={'product-detail--contain__info'}>
                        <h2 className={'product-detail--title'}>Mã sản phẩm</h2>
                        <p>
                            {product?.id}
                        </p>
                    </div>
                    <div className={'product-detail--contain__info'}>
                        <h2 className={'product-detail--title'}>Loại sản phẩm</h2>
                        <p>
                            {product?.category}
                        </p>
                    </div>
                    <div className={'product-detail--contain__info'}>
                        <h2 className={'product-detail--title'}>Nhãn hiệu</h2>
                        <p>
                            {product?.brand}
                        </p>
                    </div>
                    <hr/>
                    <div className={'product-detail--contain'}>
                        <h2 className={'product-detail--title'}>
                            Kích thước
                        </h2>
                        <div className={'product-detail--size-contain d-flex'}>
                            {
                                sizes && sizes?.map((size, index) => (
                                    <div key={index} className={'product-detail--size text-uppercase'}>{size}</div>
                                ))
                            }
                        </div>
                    </div>
                    <div className={'product-detail--contain'}>
                        <h2 className={'product-detail--title'}>
                            Màu
                        </h2>
                        <div className={'product-detail--color-contain d-flex'}>
                            {
                                colors && colors?.map((color, index) => (
                                    <div key={index} className={'product-detail--color'}>{color}</div>
                                ))
                            }
                        </div>
                    </div>
                    <hr/>
                    <div className={'input-add-item'}>
                        <Label className={'name'}>Thêm thuộc tính</Label>
                        <CreatableSelect
                            isClearable
                        />
                    </div>
                </CardBody>
            </Card>
        </div>
    )
}

export default ProductInformation
