import React, {useState} from 'react'
import Uppy from '@uppy/core'
import thumbnailGenerator from '@uppy/thumbnail-generator'
import { DragDrop } from '@uppy/react'
import icDelete from "../../../../assets/images/icons/icons/ic_delete.svg"
import imgEmpty from "../../../../assets/images/icons/icons/ic_upload_img.svg"
const ProductImageDetail = (props) => {
  const { product } = props
  const [product_image_details, setProductImages] = useState(product?.product_image_details ? product?.product_image_details : [])

  const uppyImage = new Uppy({
      meta: { type: 'avatar' },
      autoProceed: true,
      restrictions: { allowedFileTypes: ['image/*'] }
  })

  uppyImage.use(thumbnailGenerator)

  uppyImage.on('thumbnail:generated', (file, preview) => {
        const arr = { file: file, preview: preview}
        product_image_details.push(arr)
        setProductImages([...product_image_details])
        product.product_image_details = product_image_details
    })

    const uppy = new Uppy({
        meta: { type: 'avatar' },
        autoProceed: true,
        restrictions: { allowedFileTypes: ['image/*'] }
    })

    uppy.use(thumbnailGenerator)

    uppy.on('thumbnail:generated', (file, preview) => {
        const arr = { file: file, preview: preview}
        product_image_details.push(arr)
        setProductImages([...product_image_details])
        product.product_image_details = product_image_details
    })

  function deleteImage(event, product_image_detail) {
    product_image_details.splice(product_image_details.indexOf(product_image_detail), 1)
    setProductImages([...product_image_details])
    product.product_image_details = product_image_details
  }

  function chooseImage() {
      return (<DragDrop uppy={uppy} locale={{strings: {dropHereOr: "Thêm hình ảnh"}}}/>)
  }


    const renderPreview = () => {
    if (product_image_details.length) {
      return (
          <div className={'image-grid'}>
              {
                  product_image_details.map((product_image_detail, index) => (
                      <div key={index} className={'rounded'}>
                          <div className={'image'}>
                              <img src={product_image_detail?.image_url ? product_image_detail?.image_url : product_image_detail?.preview} alt="imgProduct" className={'img'}/>
                              <div  className={'btn-delete'}>
                                  <img src={icDelete} alt="delete" onClick={e => { deleteImage(e, product_image_detail) }}/>
                              </div>
                          </div>
                      </div>
                  ))
              }
          </div>
      )
    } else {
      return (
          <div className={'empty'}>
              <div className={'empty-image'}>
                  <img className={'img-contain'} src={imgEmpty} alt="empty"/>
                <p>
                    Chọn hình
                </p>
              </div>
              <DragDrop uppy={uppyImage}/>
          </div>
      )
    }
  }
  return (
      <div className={'product-detail--contain'}>
        <div className={'d-flex justify-content-between align-items-center'} style={{marginBottom: '10px'}}>
          <h3 className={'product-detail--title product-detail-title__detail mb-0'}>
            Hình ảnh mô tả sản phẩm
          </h3>
          <div className={'product-detail--add-image'}>
              {chooseImage()}
          </div>
        </div>
        <div className={'product-detail--contain-image'}>
            {renderPreview()}
        </div>
      </div>
  )
}

export default ProductImageDetail
