// ** React Imports
import React, {useState} from 'react'
import '@styles/react/libs/editor/editor.scss'
import '@styles/react/pages/product-detail.scss'
import '../styles/product.scss'
import {
  Row, Col, Card, CardBody
} from 'reactstrap'
//component
import ProductInformation from "./components/ProductInformation"
import ProductImage from "./components/ProductImage"
import ProductVariant from "./components/ProductVariant"
import ProductDescription from "./components/ProductDescription"
import ProductImageDetail from "./components/ProductImageDetail"
import ProductSpecification from "./components/ProductSpecification"
import ProductVariantMedia from "./components/ProductVariantMedia"
import AddNewButton from "../../../components/Buttons/AddNewButton"

const ProductDetail = props => {
  // ** Props
  const { product, isSubmit, variant_groups, type_values, default_type_values, dispatch, handleSubmitButton } = props

  const [variantGroup, setVariantGroup] = useState(null)

  function setVariantFromTable(variantGroup) {
    setVariantGroup(variantGroup)
  }

  return (
      <>
        <div className={'product-item mb-2'}>
          <div className='d-flex align-items-center justify-content-between pb-1'>
            <p className={'title-item'}>Sản phẩm</p>
            <button className={'btn btn-primary btn-save'} disabled={isSubmit} onClick={handleSubmitButton}>Lưu</button>
          </div>
          <Row className='product-wrapper bg-white mx-0'>
            <Col className={`col-lg-8 col-sm-12 product-detail`}>
              <Card className={'border-dark mb-0'}>
                <CardBody>
                  <div className={'product-detail--contain'}>
                    <h2 className={'product-detail--title'}>
                      Tên sản phẩm
                    </h2>
                    <input className={'product-detail--name'} type="text" defaultValue={product?.name}/>
                  </div>
                  {/*toolbar*/}
                  <ProductDescription dispatch={dispatch} product={product}/>
                  <ProductImageDetail dispatch={dispatch} product={product}/>
                  <ProductSpecification dispatch={dispatch} product={product}
                                        type_values={type_values} default_type_values={default_type_values}/>
                </CardBody>
              </Card>
            </Col>
            <Col className='col-lg-4 col-sm-12 product-detail'>
              <ProductInformation dispatch={dispatch} product={product}/>
              <ProductImage dispatch={dispatch} product={product}/>
            </Col>
          </Row>
        </div>

        <div className={'product-variant-item'}>
          <div className='d-flex align-items-center justify-content-between'>
            <p className={'title-item white-space-nowrap pb-1'}>Phiên bản sản phẩm</p>
            <AddNewButton text={'Thêm phiên bản'} link={`/product/${product?.id}/product-variant/create`}/>
          </div>
          <Row className='product-wrapper bg-white mx-0'>
            <Col className='col-lg-8 col-sm-12 product-detail'>
              <Card className={'border-dark'}>
                <CardBody>
                  <ProductVariant dispatch={dispatch} product={product} variant_groups={variant_groups} setVariantFromTable={setVariantFromTable}/>
                </CardBody>
              </Card>
            </Col>
            {
              variantGroup &&
              <Col className={`col-lg-4 col-sm-12 product-detail`}>
                <ProductVariantMedia dispatch={dispatch} product={product} variantGroup={variantGroup}/>
              </Col>
            }
          </Row>
        </div>
      </>
  )
}

export default ProductDetail
