import React from 'react'
import {Col, Row} from "reactstrap"
import ProductSpecialSpecification from "./ProductSpecialSpecification"
import ProductDefaultSpecification from "./ProductDefaultSpecification"

const ProductSpecification = (props) => {
    const { product, type_values, default_type_values, dispatch} = props
    return (
    <>
        <div className={'d-flex align-items-center'} style={{marginBottom: '10px'}}>
            <h2 className={'product-detail--title mb-0'} style={{marginRight: '15px'}}>
                Thông số kỹ thuật
            </h2>
            {/*<div className={'btn-edit'}>Chỉnh sửa</div>*/}
        </div>
        <div className='py-50 tabs-product-detail'>
            <Row className='mx-0'>
                <Col className={`col-md-6 col-sm-12`}>
                    <ProductSpecialSpecification dispatch={dispatch} product={product} type_values={type_values}/>
                </Col>
                <Col className={`col-md-6 col-sm-12`}>
                    <ProductDefaultSpecification dispatch={dispatch} product={product} default_type_values={default_type_values}/>
                </Col>
            </Row>
        </div>
    </>
)
}
export default ProductSpecification
