// ** React Imports
import React, {useEffect, Fragment, useState} from 'react'
import {Link, useHistory, useParams} from 'react-router-dom'

// ** Product detail components
import ProductDetails from './ProductDetails'

// ** Custom Components
// ** Third Party Components
// ** Store & Actions
import { useDispatch, useSelector } from 'react-redux'

import '@styles/base/pages/app-ecommerce-details.scss'
import {editProduct, getProduct} from "../store/action"

const Details = () => {
    // ** Vars
    const productId = useParams().id

    // ** Store Vars
    const dispatch = useDispatch()
    const store = useSelector(state => state.products)
    const [loading, setLoading] = useState(true)
    const [isSubmit, setSubmit] = useState(false)

    // ** ComponentDidMount : Get product
    useEffect(() => {
        dispatch(getProduct(productId))
        // dispatch(getProductVariant(productId))
        // dispatch(getGroupTypesDefault())
        setLoading(false)
    }, [dispatch])

    async function handleSubmitButton() {
        const product = store.product
        if (!isSubmit && product?.id) {
            setSubmit(true)
            const res = await editProduct(product)
            if (res?.data?.status) {
                setSubmit(false)
                store.product = null
                history?.go(-1)
            } else {
                setSubmit(false)
                //show error message
            }
        }
    }

    const setVariantsFromProduct = (variants) => {
        if (variants && variants?.length > 0) {
            const variantGroups = []
            const colors = []
            variants.forEach(variant => {
                if (variant.color !== '') {
                    let isCheck = false
                    for (const color of colors) {
                        if (color === variant.color) {
                            isCheck = true
                        }
                    }
                    if (!isCheck) {
                        colors.push(variant.color)
                    }
                }
            })
            if (colors.length === 0) {
                const variantGroup = {}
                variantGroup.sizes = []
                variantGroup.default = ''
                variantGroup.variants = []
                for (const variant of variants) {
                    variantGroup.default = variant
                    variantGroup.sizes.push(variant.size)
                    variantGroup.variants.push(variant)
                }
                variantGroups.push(variantGroup)
            } else {
                for (const color of colors) {
                    const variantGroup = {}
                    variantGroup.sizes = []
                    variantGroup.default = ''
                    variantGroup.variants = []
                    for (const variant of variants) {
                        if (color === variant.color) {
                            variantGroup.default = variant
                            variantGroup.sizes.push(variant.size)
                            variantGroup.variants.push(variant)
                        }
                    }
                    variantGroups.push(variantGroup)
                }
            }
            return variantGroups
        }
    }
    return store?.product !== null && store?.product !== undefined
    && store?.product?.id === productId ? (
        <Fragment>
            <div className='app-ecommerce-details'>
                {
                    isSubmit &&
                    <div className='loading'>
                        <div className='effect-1 effects'/>
                        <div className='effect-2 effects'/>
                        <div className='effect-3 effects'/>
                    </div>
                }
                <ProductDetails
                    isSubmit={isSubmit}
                    handleSubmitButton={handleSubmitButton}
                    dispatch={dispatch}
                    product={store?.product}
                    variants={store?.variants}
                    type_values={store?.type_values}
                    default_type_values={store?.default_type_values}
                    variant_groups={setVariantsFromProduct(store?.variants)}
                />
            </div>
        </Fragment>
    ) : (<div></div>
        // <Alert>
        //     <h4 className='alert-heading'>Sản phẩm không tìm thấy, vui lòng kiểm tra lại</h4>
        //     <div className='alert-body'>
        //         Mã sản phẩm: {productId} không tồn tại. Vui lòng kiểm tra lại danh sách sản phẩm:
        //         <Link to='/product/all'>Danh sách sản phẩm</Link>
        //     </div>
        // </Alert>
    )
}

export default Details
